<template>
	<u-sticky style="width: 100vw;" h5-nav-height="0">
		<view class="menu-box" :class="(isShowMask?'':'hide')">
			<u-dropdown  @open="openEvent" @close="closeEvent" v-if="filter" ref="uDropdown">
				<u-dropdown-item title="区服">
					<view class="slot-content" style="background-color: #FFFFFF;">
						<view class="container">
							<view class="filter-item">
								<view v-for="(ritem, rindex) in filter.regional" class="regional m-b-5">
									<view class="title zf m-b-5">
										{{ritem.name}}
									</view>
									<u-grid :col="5" :border="false">
										<u-grid-item :custom-style="{padding:'10rpx 0'}"  
										 v-for="(citem, cindex) in ritem.child"
										 :index="cindex" 
										 @click="regionalClick(citem.id)" >
											<span class="label" 
												:class="regional_ids.indexOf(citem.id) > -1? 'active': ''"
											>
											{{citem.name}}</span>
										</u-grid-item>
									</u-grid>
								</view>
							</view>
							<view class="footer-btn">
								<span class="btn btn-delete" @click="reset">重置</span>
								<span class="btn btn-confirm" @click="confirm">确定</span>
							</view>
						</view>
					</view>
				</u-dropdown-item>
				
				<u-dropdown-item title="体型">
					<view class="slot-content" style="background-color: #FFFFFF;">
						<view class="container">
							<view class="filter-item">
								<view class="school">
									<view class="viewFlex">
										<scroll-view scroll-y="true" class="tx-left">
											<view v-for="(item, index) in filter.school"
											 @click="sindex = index"
											 class="title text"
											 :class="sindex == index? 'active': ''"
											 >{{index}}</view>
										</scroll-view>
										<view class="tx-right">
											<scroll-view style="height: 260px;" scroll-y="true" v-if="sindex">
												<block v-for="(item, index) in filter.school[sindex]">
													<view class="text" 
														@click="schoolVal = item"
														:class="schoolVal == item? 'active': ''">{{item}}</view>
												</block>
											</scroll-view>
											<view class="footer-btn">
												<span class="btn btn-delete" @click="reset">重置</span> 
												<span class="btn btn-confirm" @click="confirm">确定</span>
											</view>
										</view>
									</view>
								</view>
								
							</view>
						</view>
					</view>
				</u-dropdown-item>
				
				<u-dropdown-item title="排序">
					<view class="slot-content" style="background-color: #FFFFFF;">
						<view class="container">
							<view class="filter-item">
								<view class="sort tx-right">
									<block v-for="(item, index) in filter.sort">
										<view class="text title" 
											@click="sortVal = index, confirm()"
											:class="sortVal == index? 'active': ''">{{item}}</view>
									</block>
								</view>
							</view>
						</view>
					</view>
				</u-dropdown-item>
				
				<u-dropdown-item title="筛选">
					<view class="slot-content" style="background-color: #FFFFFF;">
						<view class="container">
							<view class="filter-item">
								<view class="more">
									<view class="more-title">装备</view>
									<view class="margin-bottom-20">
										<view class="name"
											v-for="(item, index) in filter.more.option1[0]"
											:class="more_ids.indexOf(item) > -1? 'active': ''"
											 @click="moreClick(item)"
											>
											{{item}}
										</view>
										<view class="pvp-grade">
											<view class="viewFlex">
												<view class="viewFlex" style="align-self: center;">大于</view> <input class="input" v-model="moreVal" type="number">
											</view>
										</view>
									</view>
									<view class="margin-bottom-20">
										<view class="name"
											v-for="(item, index) in filter.more.option1[1]"
											:class="more_ids.indexOf(item) > -1? 'active': ''"
											 @click="moreClick(item)"
											>
											{{item}}
										</view>
									</view>
									<view class="more-title">外观</view>
									<view style="margin-top: 20rpx;">
									
										<view class="" v-for="(item, index) in filter.more.option2">
											<view 
												style="margin-bottom: 20rpx;"
												v-for="(citem, cindex) in item"
												@click="moreClick(citem.id)"
												:index="cindex" class="name"
												:class="more_ids.indexOf(citem.id) > -1? 'active': ''"
												>
												{{citem.name}}
											</view>
										</view>
									</view>
								</view>
							</view>
							<view class="footer-btn">
								<span class="btn btn-delete" @click="reset">重置</span>
								<span class="btn btn-confirm" @click="confirm">确定</span>
							</view>
						</view>
					</view>
				</u-dropdown-item>
			</u-dropdown>
		</view>
	</u-sticky>
</template>

<script>
	export default {
		props: {
			filter: {
				type: Object,
				default: () => {
					return {}
				}
			},
		},
		watch:{
			'filter.school': {
				handler(newVal, oldVal) {
					this.sindex = Object.keys(newVal)[0]
				},
				deep: true,
				immediate: true
			}
		},
		data() {
			return {
				isShowMask: false,
				regional_ids: [],
				sindex: null,
				schoolVal: null,
				sortVal: null,
				more_ids: [],
				moreVal: null
			}
		},
		methods:{
			openEvent(){
				this.isShowMask = true;
			},
			closeEvent(){
				this.isShowMask = false;
			},
			regionalClick(id){
				let set = new Set(this.regional_ids);
				set.has(id)? set.delete(id): set.add(id)
				const array = [...set]; 
				this.regional_ids = array
			},
			moreClick(id){
				let set = new Set(this.more_ids);
				
				set.has(id)? set.delete(id): set.add(id)
				const array = [...set]; 
				
				this.more_ids = array
			},
			reset(){
				this.sindex = Object.keys(this.filter.school)[0]
				this.schoolVal = null
				this.sortVal = null
				this.regional_ids = []
				this.more_ids = []
				this.moreVal = null
			},
			query(){
				let query = {
					regional_ids: this.regional_ids, // 区服ids
					schoolVal: this.schoolVal, // 体型
					more_ids: this.more_ids, // 筛选ids
					sortVal: this.sortVal, // 排序规则
				}
				return query;
			},
			confirm(){
				this.$emit("query", this.query())
				this.$refs.uDropdown.close();
			}
		}
	}
</script>

